$blue:#4bb0fe;
$blue_light:#8e9fff;

$purple:#5e71d9;
$purple_light:#8d9efc;

$red:#f14767;
$red_light:#f7738c;

$orange:#fa8c0c;
$orange_light:#fc9d2d;

$gray:#4bb0fe;
$gray_light:#8e9fff;

$green:#4bb0fe;
$green_light:#8e9fff;

$baseFont:#333;


:root {
    --blue:#4bb0fe;
    --blue_light:#8e9fff;
    --purple:#5e71d9;
    --purple_light:#8d9efc;
    --red:#f14767;
    --red_light:#f7738c;
    --orange:#fa8c0c;
    --orange_light:#ffbc6e;
    --gray:#d0cfcf;
    --gray_light:#bababa;
    --green:#27cc8f;
    --green_light:#84e0be;
    --baseFont:#333;;
  }
  
@import '../tools/mixin.scss';
@import '../tools/rwd.scss';
.nav{
    ul{
        display: flex;
        justify-content: flex-end;
        height: 60px;
        align-items: center;
        padding: 0 10px;
    }
}
.list_item{
    cursor: pointer;
    margin: 0 4px;
    i{
        padding: 0 4px;
    }
    &:hover{
        color:var(--purple);   
    }
}