$blue:#4bb0fe;
$blue_light:#8e9fff;

$purple:#5e71d9;
$purple_light:#8d9efc;

$red:#f14767;
$red_light:#f7738c;

$orange:#fa8c0c;
$orange_light:#fc9d2d;

$gray:#4bb0fe;
$gray_light:#8e9fff;

$green:#4bb0fe;
$green_light:#8e9fff;

$baseFont:#333;


:root {
    --blue:#4bb0fe;
    --blue_light:#8e9fff;
    --purple:#5e71d9;
    --purple_light:#8d9efc;
    --red:#f14767;
    --red_light:#f7738c;
    --orange:#fa8c0c;
    --orange_light:#ffbc6e;
    --gray:#d0cfcf;
    --gray_light:#bababa;
    --green:#27cc8f;
    --green_light:#84e0be;
    --baseFont:#333;;
  }
  
@import '../tools/mixin.scss';
@import '../tools/rwd.scss';
.page{
    height: 100vh;
    background-size: cover;
    padding-top: 40px;
    // background-image: url('/image/bg2.png');
}

.wrapper{
    width: 80%;
    margin: 0 auto;
}

.panel{
    display: flex;
    align-items: center;
    justify-content: center;
    justify-content: space-between;
    padding-top: 50px;
}

.item{
    height: 200px;
    width: 22%;
    cursor: pointer;
    p{
        margin-top: 20px;
    }
}

.img_box{
    height: 200px;
    display: flex;
    align-items: center;
    img{
        width: 80%;
        display: block;
        margin: 0 auto;
    }
}
